@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital@1&family=Playfair+Display:ital,wght@0,500;0,900;1,700&family=Raleway&display=swap");

//MEDIA QUERIES
$max-s: 480px;
$max-m: 600px;
$max-l: 1025px;

//COLOURS
$primary: #343539;
$secondary: #ac9b8c;
$blush: #d9d2cd;
$blue-gray: #6e6e76;
$light-gray: #f5f5f5;
$dark-grey: #c8cbd1;
$text: #343539;
$white: #ffffff;
$black: #000000;

//TYPOGRAPHY

$serif: "Playfair Display", serif !default;
$sans-serif: "Raleway", sans-serif !default;
$quote: "Raleway", sans-serif;
$italic: italic;
$bold: 700;
$extra-bold: 900;
$center: center;
$left: left;
$justify: justify;
$border: 2px solid $secondary;

$font-l: 3rem;
$font-m: 2rem;
$font-s: 1.6rem;
$font-xs: 1.4rem;

